				</view>
				<view class="df-jcsb" style="margin: 0 45rpx 17rpx; box-shadow: 0 0 7rpx #93a2ab; height: 90rpx; border-radius: 15rpx;">
					<view class="df-jcc-aic" style="margin: 0 27rpx 0 40rpx; ">
