						/>
					</view> 
					<view @click="switchDisplay2" class="df-jcc-aic" style="margin-right: 47rpx;">
						<image :src="eyeFlag2 ? srcEye1 : srcEye2" mode="widthFix" style="width: 32rpx;"></image>
					</view>
				</view>
				<view class="ta-c color-H0f649c f33rpx" style="margin: 24rpx 50rpx 25rpx; line-height: 42rpx;">
					Be sure to make your password extra special!  
					Include 8 characters, at least 1 uppercase
					letter, 1 number,  and 1 special character   
				</view>
				<view class="df" style="margin: 0 0 0 47rpx;">
					<view class="" @click="ifConsent" style="position: relative; z-index: 1;">
						<view class="" style="width: 44rpx; height: 44rpx; border-radius: 10rpx; border: 2rpx solid #93a2ab;"></view>
						<image style="position: absolute; top: 5rpx; left: 10rpx; z-index: 10; width: 50rpx;"
						:src="IfConsent? '../../static/fristPage/dui.png':'' " mode="widthFix" ></image> 
					</view>
					<view class="df f24rpx color-H0f649c" style="margin-left: 20rpx;">
						<view class="df-jcc-aic">I agree with the </view>
						<view class="df-jcc-aic" style="margin-left: .2em;">
							<view @click="goTerms" style="border-bottom: 1rpx solid #0f649c;"
							hover-class="hover" hover-start-time=0 hover-stay-time=150
							>Terms and Conditions</view>
						</view>
					</view>
				</view> 
				<view @click="goCreateLogin" 
				hover-class="hover_a24" hover-start-time=0 hover-stay-time=150
				class="bgc-o-r ta-c f33rpx FFF fw800 v_Continue1">Continue</view>
			</view> 
		</view>
		<!-- 服务条款 -->
		<view v-if="ifTerms"> 
		<view @click="backTerms" class="" style="position: absolute; top: 130rpx; left: 78rpx;"
			hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
			<image src="../../static/Index/fanHui.png" mode="widthFix" style="width: 25rpx;"></image>
		</view>
		<view class="bgc_FFF bb" style="margin: 55rpx 0 0;">
			<view class="" style="height: 60rpx;"></view>
			<view class="ta-c fw800 color-H0f649c" style="font-size: 48rpx;">Terms of Service</view>
			<view class="" style="height: 40rpx;"></view>  
			<scroll-view scroll-y="true" style="height: calc(100vh - 498rpx);">
			<view class="f21rpx color-H0f649c fw800" style="margin: 0 40rpx 40rpx ; line-height: 26rpx;">
				Definitions and key terms
			</view>
			<view class="f21rpx color-H0f649c " style="margin: 0 40rpx 40rpx;">
				To help explain things as clearly as possible in this 
				Return & Refund Policy, every time any of these terms 
				are referenced, they are strictly defined as:
				
			</view>
			<view class="f21rpx color-H0f649c" style="margin: 0 40rpx 0rpx; line-height: 26rpx;">
				<text style="margin-right: 10rpx;" class="fw800">• Company:</text> 
				when this policy mentions‚the company,we‚us, 
				TekyGo!‚ TekyGo! Service ‚ Our company‚ our, service provider, it
				refers to TekyGo!, that is responsible for your information under 
				this Return & Refund Policy.
			</view>
			<view class="f21rpx color-H0f649c" style="margin: 0 40rpx 0rpx; line-height: 30rpx;">
				<text style="margin-right: 10rpx;" class="fw800">• Customer:</text> 
				refers to the company, organization or person that
				signs up to use the Tekygo! Service to manage the relationships 
				with your consumers or service users.
			</view>
			<view class="f21rpx color-H0f649c" style="margin: 0 40rpx 0rpx; line-height: 30rpx;">
				<text style="margin-right: 10rpx;" class="fw800">• Device:</text> 
				 any internet connected device such as a phone, tablet, 
				computer or any other device that can be used to visit the TekyGo! 
				App or Website and is about to use our services.
			</view>
			<view class="f21rpx color-H0f649c" style="margin: 0 40rpx 0rpx; line-height: 30rpx;">
				<text style="margin-right: 10rpx;" class="fw800">• Service:</text> 
				 refers to the service provided by TekyGo! as described 
				in the relative terms (if available) and on this platform.
			</view>
			<view class="f21rpx color-H0f649c" style="margin: 0 40rpx 0rpx; line-height: 30rpx;">
				<text style="margin-right: 10rpx;" class="fw800">• Website:</text> 
				 TekyGo! website which can be accessed via this URL: 
				<text @click="goUrl" style="color: #eb5120;">https://tekygo.com/</text>
			</view>
			<view class="f21rpx color-H0f649c" style="margin: 0 40rpx 0rpx; line-height: 30rpx;">
				
				• You,user,customer,individual: a person or entity that is 
				registered with Tekygo! to use the Services.
			</view>
			<view class="f21rpx color-H0f649c fw800" style="margin: 40rpx ; ">
				Return & Refund Policy
			</view>
			<view class="f21rpx color-H0f649c" style="margin: 0 40rpx 0; line-height: 30rpx;">
				Not 100% satisfied with your purchase? Most unopened items may 
				be returned within 30 days of delivery shipment date for a 
				full refund. See Exclusions below.
				
				The return policy is valid for 30 days from the delivery 
				shipment date. Shipping/Delivery costs will not be refundable. 
				Products must be in original box with original accessories, 
				packaging, manuals, registration and undamaged/same condition 
				purchased in will be eligible for a full refund/return. 
				
				If product returned has any customer data or information 
				stored on device storage or memory, TekyGo! will not be 
				responsible for transferring of mentioned data/information to 
				another device, loss of data or maintaining confidentiality 
				of data/information left in device upon an exchange for another product. 
				
				Original Receipt/packing slip must accompany any product(s) 
				that are being returned. 
			</view>
			<view class="" style="height: 100rpx; f33"></view>
			<view class="bgc-o-r FFF ta-c" @click="Agree"
			hover-class="hover_a24" hover-start-time=0 hover-stay-time=150 
			style="margin: 0 110rpx 40rpx; height: 90rpx; border-radius: 45rpx; line-height: 90rpx;">
				Agree
			</view>
			<view class="bgc-o-r FFF ta-c f33rpx" @click="Disagree" 
			hover-class="hover_a24" hover-start-time=0 hover-stay-time=150 
			style="margin: 0 110rpx 40rpx; height: 90rpx; border-radius: 45rpx; line-height: 90rpx;">
				Disagree
			</view>
			<view class="" style="height: 100rpx;"></view>
		</scroll-view>
		</view>
		</view>
	</view> 

</template>

<script>
	import terms from './termsSevice.vue'
	export default{
		components:{
			terms
		},
		data(){
			return{
				noinputEmail:'',
				firstName:'',
				lastName:'',
				country:'Country',
				city:'',
				ifInputEmail:true,
				ifInputName:false,
				ifCountry:false,
				createPassword:false,
				ifTerms:false,
				password:'',
				cPassword:'',
				IfConsent:false,
				eyeFlag1:true,
				eyeFlag2:true,
				percent:0,
				activeColor:'#ff0000',
				srcEye1:"../../static/fristPage/by_Y.png",
				srcEye2:'../../static/fristPage/zhenyan.png', 
				times:null,
				pagePost:true
			} 
		},
		watch:{
			password(newVal,oldVal){
				let NumStr = new RegExp("^(?=.*[A-Z])(?=.*[0-9])", "g");
				let txNum = new RegExp("^(?=.*[0-9])(?=.*\\W).*$", "g");
				let tw = new RegExp("^(?=.*[A-Z])(?=.*[0-9])(?=.*\\W).*$", "g");
				let all = new RegExp("^(?=.{8,})(?=.*[A-Z])(?=.*[0-9])(?=.*\\W).*$", "g");
				if(newVal.length >= 1){
					this.percent = 20
					this.activeColor = '#ff0000'
					if(NumStr.test(newVal) ||txNum.test(newVal)){
							this.percent = 40
							this.activeColor = '#ff0000'
							if(tw.test(newVal)){
								this.percent = 60
								this.activeColor = '#ff0000'
								if(all.test(newVal) ){ 
									this.percent = 60
									this.activeColor = '#07b603'
								}
							}
					}
				}else{
					this.percent = 0
				}
				
			}
		},
		created() {
			// console.log('触发ss');
			this.pagePost = uni.getStorageSync('ifmy')
			// console.log(this.pagePost);
		},
		methods:{
			goUrl(){
				plus.runtime.openURL('https://tekygo.com/');
			},
			goGuest(){
				uni.removeStorageSync('ifmy')
				uni.removeStorageSync('token')
				uni.setStorageSync('isLogin',false)
				uni.switchTab({
					url:'../index/index'
				})
			},
			goTerms(){
				this.ifInputEmail = false
				this.ifInputName = false
				this.createPassword = false
				this.ifTerms = true 
			},
			backTerms(){
				this.ifTerms = false
				this.createPassword = true
				// this.IfConsent = true
			},
			Agree(){
				this.ifTerms = false
				this.createPassword = true
				this.IfConsent = true
			},
			Disagree(){
				this.ifTerms = false
				this.createPassword = true
				this.IfConsent = false
			},
			// 切换显示密码
			switchDisplay1(){
				this.eyeFlag1 = !this.eyeFlag1
			},
			switchDisplay2(){
				this.eyeFlag2 = !this.eyeFlag2
			},
			backInputEmail(){
				uni.removeStorageSync('ifmy')
				uni.redirectTo({
					url:'./inputEmail'
				})
			},
			backInputName(){
				this.ifInputEmail = true
				this.ifInputName = false
				this.createPassword = false
			},
			backCreatePassword(){
				this.ifInputEmail = false
				this.ifInputName = true
				this.createPassword = false
			},
			clickCounty(){
				this.ifCountry = !this.ifCountry
			},
			cilckCanada(){
				this.country = 'Canada'
				this.city = 'CA'
				if(this.ifCountry){
					this.ifCountry = false
				}
			},
			clickUSA(){
				this.country = 'America/USA'
				this.city = 'US'
				if(this.ifCountry){
					this.ifCountry = false
				}
			},
			//注册流程 
			goRegistraContinue(){
				if(this.firstName == ''){
					this.$refs.inputpopUp1.openPopup('Enter First Name')
					return false
				}
				if(this.lastName == ''){
					this.$refs.inputpopUp1.openPopup('Enter Last Name')
					return false
				}
				if(this.city == ''){
					this.$refs.inputpopUp1.openPopup('Please select address')
					return false
				}
				this.ifInputEmail = false
				this.ifInputName = false
				this.createPassword = true
			},
			ifConsent(){
				this.IfConsent = !this.IfConsent
			},
			//登录
			async goCreateLogin(){ 
				var _this = this
				console.log('点击');
				let rs = new RegExp("^(?=.{8,})(?=.*[A-Z])(?=.*[0-9])(?=.*\\W).*$", "g");
				if(this.password == '' && this.cPassword == ""){
					this.$refs.inputpopUp1.openPopup('Enter Password')
					return false
				}
				if(! rs.test(this.password)){
					this.$refs.inputpopUp1.openPopup('Your password must include 8 characters and at least 1 uppercase letter, 1 number and 1 special character.')
					return false
				}
				if(this.password != this.cPassword){
					this.$refs.inputpopUp1.openPopup('Passwords don’t match')
					return false
				}
				if(!this.IfConsent){
					this.$refs.inputpopUp1.openPopup('See Terms and Conditions First')
					return false	
				}
				this.$refs.passwLoad1.openLoad()
				const {data:res} = await this.$myRequest({
					url:'/api/auth/register',
					method:'POST',
					data:{
						"email":this.noinputEmail,
						"password":this.password,
						"password_confirmation":this.cPassword,
						"first_name":this.firstName,
						"last_name":this.lastName,
						"countryCode":this.city
					}
				}) 
				console.log(res);
				if(res!= '') {
				} 
				if(res.succeeded){
					uni.setStorageSync('Email',this.noinputEmail)
					let i = 0
					_this.times= setInterval(function () {
						i+=1
						_this.Login()
					  console.log('触发啦');
						console.log(i);
						if(i>=20){
							i=0
							_this.$refs.passwLoad1.closeLoad()
							clearInterval(_this.times)
							uni.reLaunch({
								url:'./inputEmail'
							})
							// return false
							// console.log(i);
						}
					}, 2000);
					// uni.setStorageSync('Password',this.password)
					// uni.setStorageSync('Email',this.noinputEmail)
					// uni.redirectTo({
					// 	url:"./registerWin"
					// }) 

				}else{
					this.$refs.inputpopUp1.openPopup(res.errmsg)
					_this.$refs.passwLoad1.closeLoad()
				}
			},
			
			// 输入邮箱后验证邮箱
			async goNoEmailContinue(){
				console.log(this.noinputEmail);
				var that = this 
				if(this.noinputEmail != ''){
					this.$refs.passwLoad1.openLoad()
					const {data:res} = await this.$myRequest({
						url:'/api/auth/get'+'?email=' + this.noinputEmail,
					})
					console.log(res);
					if(res!= '') {
						this.$refs.passwLoad1.closeLoad()
					}
					if(res.errno == 303){
						console.log('邮箱不存在');
						this.ifInputEmail = false
						this.ifInputName = true
						return false
					}else if(res.succeeded){
						console.log('邮箱存在');
						uni.redirectTo({ 
							url:'./inputPassword' 
						})
					}else if(res.errno != 303 && res.errno != 0){
						this.$refs.inputpopUp1.openPopup(res.errmsg) 
						return false
					}
				}
				else{
					this.$refs.inputpopUp1.openPopup('Enter your email') 
					return false
				}
			},
			//登录
			async Login(){
				const {data:resd} = await this.$myRequest({
					method:'POST',
					url:'/api/auth/login',
					data:{
						"email":this.noinputEmail,
						"password":this.password
					}
				}) 
				console.log(resd);
				let _this = this
				if(resd.succeeded){
					clearInterval(_this.times)
					let token = resd.data.token
					console.log(token);
					console.log('成功');
					uni.setStorageSync('token',token)
					uni.setStorageSync('Password',this.inputPassword)
					uni.setStorageSync('Email',this.noinputEmail)
					uni.redirectTo({
						url:"./registerWin"
					}) 
				}else{
					// this.$refs.inputpopUp1.openPopup(resd.errmsg)
					// return false
				}				 
			},
			
		},
	}
